import type { Policies, Policy } from '../../../shared/generated-types/graphql';

export type UnformattedPolicy = Policy & { applicationId: string; leaseId: number; paymentId: number };

const formatPolicy = (policy: UnformattedPolicy): Policy => {
  return {
    ...policy,
    applicationId: {
      id: policy.applicationId,
    },
    leaseId: {
      id: policy.leaseId,
    },
    paymentId: {
      id: policy.paymentId,
    },
  };
};

export const buildPoliciesFromResponse = (response: { policies?: Array<UnformattedPolicy> }): Policies => {
  const formattedPolicies = response?.policies?.map((policy) => formatPolicy(policy)) || [];

  return { policies: formattedPolicies };
};
